<template>
  <div>
    <el-tag :key="index"
            :disable-transitions="false"
            :effect="tag.status === 'choosed'? 'dark' : 'plain'"
            @click.native="chooseTag(tag)" style="margin-right: 10px;" v-for="(tag, index) in TagsArr">{{tag.name}}</el-tag>
    <span v-if="updateStatus">
      <el-input
        class="input-new-tag"
        v-if="inputShowCondition.value"
        v-model="model.value"
        ref='saveTagCharacter'
        size="small"
        @keyup.enter.native="handleInputConfirm"
        @blur="handleInputConfirm">
      </el-input>
      <el-button v-else class="button-new-tag" size="small" @click="showInput(type)">添加{{type}}</el-button>
    </span>
  </div>
</template>
<script>
import { addSkillCertService, addCharacterService } from '../../service/AuntManage'
export default {
  props: ['TagsArr', 'updateStatus', 'serviceTypeId', 'type', 'inputShowCondition', 'model'],
  data () {
    return {
    }
  },
  methods: {
      // 选择为自己的标签，修改为选中状态
    chooseTag (tag) {
      if (this.updateStatus === true) {
        tag.status === 'choosed' ? tag.status = '' : tag.status = 'choosed'
        this.$forceUpdate()
      }
    },
    // 标题添加显示输入框
    showInput (type) {
      this.inputShowCondition.value = true
      console.log(this.$refs)
    },
    // 技能标签输入完成确定并提交
    handleInputConfirm () {
      console.log(this.serviceTypeId)
      if (this.type === '技能') {
        if (this.serviceTypeId === '' || !this.serviceTypeId) {
          this.$message('请先选择服务类型')
          this.inputShowCondition.value = false
          this.model.value = ''
          return false
        }
        let inputValue = this.model.value
        if (inputValue) {
          let tag = {}
          tag.name = inputValue
          console.log(this.serviceTypeId)
          tag.service_type_id = this.serviceTypeId
          tag.category = 'skills-label'
          this.addSkillCert(tag)
          // this.TagsArr.push(tag)
        }
        this.inputShowCondition.value = false
        this.model.value = ''
      }
      if (this.type === '性格') {
        let CharaValue = this.model.value
        if (CharaValue) {
          let tag = {}
          tag.name = CharaValue
          this.addCharacter(tag)
          // this.TagsArr.push(tag)
        }
        this.inputShowCondition.value = false
        this.model.value = ''
      }
      if (this.type === '证书') {
        if (this.serviceTypeId === '' || !this.serviceTypeId) {
          this.$message('请先选择服务类型')
          this.inputShowCondition.value = false
          this.model.value = ''
          return false
        }
        let CertificateValue = this.model.value
        if (CertificateValue) {
          let tag = {}
          tag.name = CertificateValue
          tag.service_type_id = this.serviceTypeId
          tag.category = 'certificate-label'
          this.addSkillCert(tag)
          // this.TagsArr.push(tag)
        }
        this.inputShowCondition.value = false
        this.model.value = ''
      }
    },
    // 添加性格到服务器
    addCharacter (param) {
      addCharacterService(param).then((res) => {
        this.TagsArr.push(res.data)
        this.TagsArr[this.TagsArr.length - 1].status = 'choosed'
      })
    },
    // 添加技能/证书到服务器
    addSkillCert (param) {
      addSkillCertService(param).then((res) => {
        this.TagsArr.push(res.data)
        this.TagsArr[this.TagsArr.length - 1].status = 'choosed'
      })
    }
  }
}
</script>
<style>
  .el-input.is-disabled .el-input__inner {
    /* background: #fff; */
    color: #000
  }
  .el-tag{
  cursor: pointer;
  margin-bottom: 11px;
  }
  .input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
</style>



