<template>
  <div id="upload-video">
    <el-upload
      :show-file-list="true"
      :file-list="fileList"
      class="upload-demo"
      action="default"
      :limit="1"
      :before-upload="beforeAvatarUpload"
      :http-request="uploadAction">
      <el-button size="small" type="primary"> <span class="red"> * </span><span class="needsclick">点击上传视频</span></el-button>
      <div slot="tip" class="el-upload__tip">视频格式只支持：MP4，大小不超过200M</div>
    </el-upload>
  </div>
</template>
<script>
import { uploadImage, uploadoss, uploadFile } from '../service/loginServer.js'
export default {
  name: 'uploadVideo',
  props: [ 'use' ],
  data () {
    return {
      fileList: [],
      uploadHeaders: {
        'Authentication-Token': localStorage.getItem('token')
      }
    }
  },
  methods: {
    uploadTest (item) {
      let fd = new FormData()
      fd.append('photo_id', item.file)
      uploadImage(fd).then(res => {
        let params = {
          path: res.data.file_path,
          use: this.use
        }
        console.log(params)
        // 通知父级元素，返回图片路径
        this.$emit('uploadSucc', params)
      })
    },
    uploadAction (item) {
      this.fileList = []
      let ossParams = { 'upload_type': 'admin' }
      uploadoss(ossParams).then(res => {
        let params = {
          host: res.data.host
        }
        let fd = new FormData()
        let postfix = item.file.name.split('.')[item.file.name.split('.').length - 1]
        let filename = this.$md5(`${new Date().getTime()}_${item.file.name}`)
        fd.append('callback', res.data.callback)
        fd.append('success_action_status', '200')
        fd.append('OSSAccessKeyId', res.data.accessid)
        fd.append('policy', res.data.policy)
        fd.append('signature', res.data.signature)
        fd.append('key', `${res.data.dir}${filename}.${postfix}`)
        fd.append('host', res.data.host)
        fd.append('file', item.file)
        fd.append('expire', res.data.expire)
        fd.append('name', `${filename}.${postfix}`)
        params['fd'] = fd
        uploadFile(params.host, params.fd).then(res => {
          let result = {
            path: `/${res.filename}`,
            host: `${params.host}`,
            use: this.use
          }
          this.fileList = [{url: res.filename, name: res.filename}]
          // 通知父级元素，返回图片路径
          this.$emit('uploadSucc', result)
        })
      })
    },
    beforeAvatarUpload (file) {
      const isLt2M = file.size / 1024 / 1024 < 200
      const isCurType = /\.(mp4|MP4|MOV|mov)$/.test(file.name)
      if (!isLt2M) {
        this.$alert('上传视频大小不能超过200M！', '友情提示', {
          confirmButtonText: '确 定',
          callback: action => {}
        })
      }
      if (!isCurType) {
        this.$alert('上传视频类型不符合要求!', '友情提示', {
          confirmButtonText: '确 定',
          callback: action => {}
        })
      }
      return isLt2M && isCurType
    },
    remove (file, fileList) {
      let params = {
        file,
        fileList,
        use: this.use
      }
      this.$emit('removeFile', params)
    }
  }
}
</script>
<style scoped>
.upload-demo{
  display: flex;
  flex-direction: column;
  align-items: center
}
.red {
  color: red
}
</style>



