// 转换从服务器请求回来的学历数据
export const transEducationData = function (educationList) {
  let result = educationList.map(item => {
    let temp = {}
    temp['value'] = item.id
    temp['label'] = item.name
    temp['category'] = item.category
    return temp
  })
  return result
}

