<template>
<div class="entering-aunt">
  <el-card style="overflow:auto">
  <div class="info">
    <header>基本信息</header>
    <div class="info-content">
      <div class="info-left">
        <upload-photo use="ayiPhoto" @uploadSucc="uploadSucc">
          <img v-if="AuntInfo.photo !== ''" :src="AuntInfo.photo" class="avatar needsclick">
          <i v-else class="el-icon-plus avatar-uploader-icon needsclick"
          style='width: 130px;
                 font-size:10px;
                 height: 130px;
                 line-height:130px;
                 border: 1px solid;
                 border-radius: 50%;'>
             <span class="needsclick"><span class="red"> * </span>点击上传阿姨照片</span>
          </i>
        </upload-photo>
        <div style="text-align:center; font-size: 10px; color: #606266; margin-bottom: 30px;">
          图片格式只支持：BMP、JPEG、JPG、PNG，大小不超过20M
        </div>
        <upload-video use='ayiVideo' @uploadSucc="uploadSucc"></upload-video>
      </div>
       <!-- 第一列 -->
      <div class="info-center-left">
        <div>
          <span> <span class="red"> * </span>阿姨姓名</span>
          <el-input v-model="AuntInfo.title" placeholder="请输入阿姨姓名"></el-input>
        </div>
        <div>
          <span><span class="red"> * </span>服务类别</span>
          <el-select @change="getTag()" v-model="AuntInfo.service_type_id" placeholder="请选择服务类型">
            <el-option
              v-for="item in serveTypeOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div>
          <span> <span class="red"> * </span>阿姨状态</span>
            <el-select v-model="AuntInfo.service_status"  placeholder="请选择阿姨的服务状态">
              <el-option
                v-for="item in optionsStatus"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
        </div>
        <!-- <div>
          <span>*所属家政公司</span>
          <el-select v-model="AuntInfo.housework_company_id" placeholder="请选择所属家政公司">
            <el-option
              v-for="item in companyOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div> -->
        <div>
          <span> <span class="red"> * </span>手机号码</span>
          <el-input v-model="AuntInfo.phone" placeholder="请输入手机号码"></el-input>
        </div>
        <div>
          <span><span class="red"> * </span>自我介绍</span>
          <el-input type="textarea"
                    :autosize="{ minRows: 6, maxRows: 4}"
                    placeholder="请填写阿姨的自我介绍"
                    v-model="AuntInfo.self_intro">
          </el-input>
        </div>
      </div>
      <!-- 第二列 -->
      <div class="info-center-right">
        <div>
          <span> <span class="red"> * </span>身份证号</span>
          <el-input @change='getBirthdayAndAge()' v-model="AuntInfo.id_card" placeholder="请输入身份证号"></el-input>
        </div>
        <div>
          <span>出生日期</span>
          <el-input v-model="AuntInfo.birthday" disabled placeholder="系统自动显示"></el-input>
        </div>
        <div>
          <span>年龄</span>
          <el-input v-model="AuntInfo.Age" disabled placeholder="系统自动显示"></el-input>
        </div>
        <div>
          <span> <span class="red"> * </span>学历</span>
          <el-select v-model="AuntInfo.education" placeholder="请选择学历">
            <el-option
              v-for="item in educationOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div>
          <span> <span class="red"> * </span>籍贯</span>
            <div style="width: 100%;">
              <el-cascader :props="props" @change="handleChangePlace" style="width: 100%;"></el-cascader>
            </div>
        </div>
        <div>
          <span> <span class="red"> * </span>现居地</span>
          <div style="width: 100%;">
            <div>
              <el-cascader :props="props" style="width: 100%;margin-bottom: 1px;" @change="handleChangeLive"></el-cascader>
            </div>
            <el-input type="textarea"
                      :autosize="{ minRows: 2, maxRows: 4}"
                      placeholder="请输入现居地址"
                      v-model="AuntInfo.addr">
            </el-input>
          </div>
        </div>
      </div>
      <div class="info-right">
        <div>
          <span> <span class="red"> * </span>等级</span>
          <el-select @change='changeLevel'
                     v-model="AuntInfo.level_id"
                     placeholder="请选择等级">
            <el-option
              v-for="item in levelOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
        <div>
          <span> <span class="red"> * </span>经验</span>
          <el-input v-model="AuntInfo.work_experience" type="number" placeholder="（单位：个月）"></el-input>
        </div>
        <div>
          <span> <span class="red"> * </span>薪资</span>
          <el-input v-model="AuntInfo.salary" disabled placeholder="根据等级自动展示薪资"></el-input>
        </div>
      </div>
    </div>
  </div>
  </el-card>
  <el-card style="overflow:auto;margin-top:30px">
  <div class="jobSkill">
    <h2>工作技能</h2>
    <div>
      <span><span class="red">*</span>技 能</span>
      <Tag :TagsArr="skillTags"
            :model='inputValue'
            :inputShowCondition="inputVisible"
            :updateStatus='updateStatus'
            :serviceTypeId="AuntInfo.service_type_id"
            type='技能'>
      </Tag>
    </div>
    <div>
      <span><span class="red">*</span>性 格</span>
      <Tag :TagsArr="characterTags"
            :model='CharaValue'
            :inputShowCondition='Chara'
            :updateStatus='updateStatus'
            :serviceTypeId="AuntInfo.service_type_id"
            type='性格'></Tag>
    </div>
    <div>
      <span><span class="red">*</span>证 书</span>
      <div class="certificateDiv">
        <span>
          <Tag :TagsArr="certificateTags"
                :model='CertificateValue'
                :inputShowCondition="CertificateVisible"
                :updateStatus='updateStatus'
                :serviceTypeId="AuntInfo.service_type_id" type='证书'>
          </Tag>
        </span>
        <div style="margin-top:30px">
          <upload-images use="cert" updateStatus="true" :isShowFileList="true" :fileList="certificateFileList"
            @uploadSucc="uploadSucc" @removeFile="removeFile"></upload-images>
        </div>
      </div>
    </div>
    <div>
      <span>工作风采</span>
      <div style="border-bottom:none">
        <div>
          <upload-images use="work" updateStatus="true" :isShowFileList="false" :fileList="workPhotosFileList"
            @uploadSucc="uploadSucc" @removeFile="removeFile"></upload-images>
        </div>
      </div>
    </div>
      <div>
      <span class="idcard-label">
        <span class="red">*</span>
          身份证信息
      </span>
      <div id="id-card-content">
        <div class="id-card-content-item">
         <upload-image image-size='50' use="idCardFront" @uploadSucc="uploadSucc">
            <img :fit="fit" v-if="AuntInfo.id_hold" :src="AuntInfo.id_hold" class="avatar needsclick">
            <div v-else>
              <i class="el-icon-plus avatar-uploader-icon addImgborder needsclick"></i>
            </div>
         </upload-image>
          <span class="idcard-upload-text">
           请上传身份证正面
          </span>
        </div>
        <div class="id-card-content-item">
          <upload-image use="idCardReverseSide" image-size='50' @uploadSucc="uploadSucc">
            <img :fit="fit" v-if="AuntInfo.id_back" :src="AuntInfo.id_back" class="avatar needsclick">
            <div v-else>
              <i class="el-icon-plus avatar-uploader-icon addImgborder needsclick"></i>
            </div>
          </upload-image>
          <span class='idcard-upload-text'>
          请上传身份证反面
          </span>
        </div>
      </div>
    </div>
  </div>
  <el-button type="primary" @click="submit" size="medium" style="display: flex;margin: 0 auto">提交</el-button>
  </el-card>
</div>
</template>
<script>
  import {getTypeService,
    getSkillService,
    getCharacterService,
    getCertificateService,
    getPlaceService,
    getLevelService,
    getStatusService,
    addAunt,
    getBasicInformation
  } from '../../service/AuntManage'
  import { transEducationData } from './AuntDetailConfig.js'
  import { uploadImage } from '../../service/loginServer.js'
  import Tag from './Tag.vue'
  import UploadImages from '../../components/UploadImages.vue'
  import UploadImage from '../../components/UploadImage.vue'
  import UploadVideo from '../../components/UploadVideo.vue'
  import UploadPhoto from '../../components/UploadPhoto.vue'
  import { getBirthdayFromIdCard, GetAge } from '../../assets/common/utils.js'
  export default {
    components: {Tag,
      'upload-image': UploadImage,
      'upload-images': UploadImages,
      'upload-video': UploadVideo,
      'upload-photo': UploadPhoto},
    data () {
      return {
        pid: '-1',
        options: [],
        props: {},
        fileList: [],
        fit: 'contain',
        updateStatus: true,
        workPhotosFileList: [],
        certificateFileList: [],
        AuntInfo: {
          photo: '',
          video: '',
          title: '',
          id_card: '',
          addr_province: '',
          addr_city: '',
          phone: '',
          self_intro: '',
          birthday: '',
          bankcard_number: '',
          birthplace_county_id: '',
          birthplace_province_id: '',
          birthplace_city_id: '',
          work_experience: '',
          service_type_id: 1,
          salary: '',
          addr: '',
          housework_company_id: JSON.parse(sessionStorage.userInfo).company_id,
          service_label: '',
          nature: '',
          level_id: '',
          id_hold: '',
          id_back: '',
          service_status: ''
        },
        serveTypeOptions: [],
        educationOptions: [],
        addressOptions: [],
        placeOptions: [],
        levelOptions: [],
        imageUrl: '',
        skillTags: [],
        optionsStatus: '',
        inputValue: {value: ''},
        inputVisible: {value: false},
        CharaValue: {value: ''},
        Chara: {value: false},
        CertificateVisible: {value: false},
        CertificateValue: {value: ''},
        characterTags: [],
        certificateTags: [],
        certificateImages: [],
        workPhotos: [],
        dialogImageUrl: '',
        dialogVisible: false
      }
    },
    created () {
      this.$store.dispatch('commitChangeTopTitles', ['阿姨管理', '阿姨信息', '添加阿姨'])
      let that = this
      this.props = {
        lazy: true,
        lazyLoad (node, resolve) {
          const { level } = node
          let param = {}
          if (node.data) {
            that.pid = node.data.id
          }
          param.id = that.pid
          getPlaceService(param).then((res) => {
            let resPlaceArray = res.data
            resPlaceArray.forEach((element, index) => {
              if (element.code === '810000') {
                resPlaceArray.splice(index, 2)
              }
              element.value = element.code
              element.id = element.id
              element.label = element.name
              element.leaf = level >= 1
            })
            resolve(resPlaceArray)
          })
        }
      }
      this.getTypeOption()
      this.getStatusOption()
      this.getSkillTag()
      this.getCharacterTags()
      this.getCertificateTags()
      this.getLevel()
      this.getEducations()
    },
    methods: {
      getTag () {
        if (this.AuntInfo.level_id) {
          this.AuntInfo.level_id = null
        }
        this.getLevel()
        this.getSkillTag()
        this.getCertificateTags()
      },
      getBirthdayAndAge () {
        console.log(this.AuntInfo.id_card)
        let idCardString = this.AuntInfo.id_card.toString()
        let birthday = getBirthdayFromIdCard(idCardString)
        let Age = GetAge(idCardString)
        if (isNaN(Age)) {
          Age = ''
        }
        this.AuntInfo.birthday = birthday
        this.AuntInfo.Age = Age
      },
      // 获取上户状态
      getStatusOption () {
        getStatusService().then((res) => {
          let statusTempArray = []
          let resStatusobj = res.data
          for (const key in resStatusobj) {
            let obj = {}
            obj.label = resStatusobj[key]
            obj.value = key
            statusTempArray.push(obj)
          }
          this.optionsStatus = statusTempArray
        })
      },
      // 上传头像
      uploadPhoto (param) {
        let fd = new FormData()
        fd.append('photo_id', param.file)
        uploadImage(fd).then(res => {
          this.AuntInfo.photo = res.data.file_path
        })
      },
      // 上传证书
      uploadCert (param) {
        let fd = new FormData()
        fd.append('photo_id', param.file)
        uploadImage(fd).then(res => {
          this.certificateImages.push(res.data.file_path)
        })
      },
      // 上传工作风采
      uploadWork (param) {
        let fd = new FormData()
        fd.append('photo_id', param.file)
        uploadImage(fd).then(res => {
          console.log(res.data.file_path)
          this.workPhotos.push(res.data.file_path)
        })
      },
      // 整理参数
      sorting () {
        let serviceLabel = []
        let characterLabel = []
        this.skillTags.forEach((item) => {
          if (item.status === 'choosed') {
            serviceLabel.push(item.category + item.id)
          }
        })
        this.certificateTags.forEach((item) => {
          if (item.status === 'choosed') {
            serviceLabel.push(item.category + item.id)
          }
        })
        this.characterTags.forEach((item) => {
          if (item.status === 'choosed') {
            characterLabel.push(item.id)
          }
        })
        this.AuntInfo.service_label = serviceLabel.join(',')
        this.AuntInfo.nature = characterLabel.join(',')
        let certificateImages = [...this.certificateImages]
        certificateImages = certificateImages.map((item) => {
          item = item.split(this.ossHost)[1]
          console.log(item)
          return item
        })
        let workPhotos = [...this.workPhotos]
        workPhotos = workPhotos.map((item) => {
          item = item.split(this.ossHost)[1]
          console.log(item)
          return item
        })
        this.AuntInfo.cert_image = certificateImages
        this.AuntInfo.work_style = workPhotos
        let param = {...this.AuntInfo}
        let photoPath = this.AuntInfo.photo
        param.photo = photoPath.split(this.ossHost)[1]
        let videoPath = this.AuntInfo.video
        param.video = videoPath
        if (videoPath.indexOf(this.ossHost) !== -1) {
          param.video = videoPath.split(this.ossHost)[1]
        }
        param.id_hold = param.id_hold.split(this.ossHost)[1]
        param.id_back = param.id_back.split(this.ossHost)[1]
        console.log(param)
        return param
      },
      // 表单提交参数验证
      verify (sendParams) {
        let flag = true
        if (!this.AuntInfo.photo) {
          this.$message('请上传头像')
          return
        }
        if (!this.AuntInfo.video) {
          this.$message('请上传视频')
          return
        }
        if (!this.AuntInfo.title) {
          this.$message('请输入姓名')
          return
        }
        if (!this.AuntInfo.service_type_id) {
          this.$message('请选择服务类型')
          return
        }
        if (!this.AuntInfo.service_status) {
          this.$message('请选择服务状态')
          return
        }
        if (!this.AuntInfo.phone) {
          this.$message('请输入手机号')
          return
        }
        if (!(/^1(3|4|5|7|8)\d{9}$/.test(this.AuntInfo.phone))) {
          this.$message('手机号格式错误')
          return
        }
        if (!this.AuntInfo.id_card) {
          this.$message('请填写身份证号')
          return
        }
        var cardreg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
        if (!cardreg.test(this.AuntInfo.id_card)) {
          this.$message('身份证格式错误')
          return
        }
        if (!this.AuntInfo.education) {
          this.$message('请输入选择学历')
          return
        }
        if (!this.AuntInfo.birthplace_province_id) {
          this.$message('请选择籍贯')
          return
        }
        if (!this.AuntInfo.addr_province) {
          this.$message('请选择现居地地址')
          return
        }
        if (!this.AuntInfo.work_experience) {
          this.$message('请填写工作经验')
          return
        }
        if (!this.AuntInfo.level_id) {
          this.$message('请选择等级')
          return
        }
        if (!this.AuntInfo.service_label || this.AuntInfo.service_label.indexOf('skills-label') === -1) {
          this.$message('请检查是否未选择技能标签')
          return
        }
        if (!this.AuntInfo.service_label || this.AuntInfo.service_label.indexOf('certificate-label') === -1) {
          this.$message('请检查是否未选择证书标签')
          return
        }
          // 特殊说明：由于技能证书标签为同一字段名service_label，但验证要做区分，验证通过之后重新组织参数
        let serviceLabelArr = this.AuntInfo.service_label.split(',')
        let serviceLabelArrSort = []
        serviceLabelArr.forEach((item) => {
          if (item.indexOf('skills-label') !== -1) {
            serviceLabelArrSort.push(item.split('skills-label')[1])
          }
          if (item.indexOf('certificate-label') !== -1) {
            serviceLabelArrSort.push(item.split('certificate-label')[1])
          }
        })
        sendParams.service_label = serviceLabelArrSort.join(',')
        if (!this.AuntInfo.nature || this.AuntInfo.nature === '') {
          this.$message('请选择选择性格标签')
          return
        }
        if (!this.AuntInfo.id_hold || this.AuntInfo.id_hold === '') {
          this.$message('请上传身份证正面')
          return
        }
        if (!this.AuntInfo.id_back || this.AuntInfo.id_back === '') {
          this.$message('请上传身份证反面')
          return
        }
        return flag
      },
      // 提交
      submit () {
        let sendParams = this.sorting()
        if (!this.verify(sendParams)) { return }
        addAunt(sendParams).then((res) => {
          if (res.status === 'success') {
            this.$message({type: 'success', message: '添加成功'})
            this.$router.push('/AuntList')
          } else {
            this.$message(res.msg)
          }
        })
      },
      // 获取等级和薪资
      getLevel () {
        let params = {
          type: this.AuntInfo.service_type_id
        }
        getLevelService(params).then((res) => {
          let levelOptions = res.data
          levelOptions.forEach(element => {
            element.value = element.id
            element.label = element.level_name
          })
          this.levelOptions = levelOptions
        })
      },
      // 获取技能标签
      getSkillTag () {
        let param = {}
        param.category = 'skills-label'
        param.service_type_id = this.AuntInfo.service_type_id
        getSkillService(param).then((res) => {
          this.skillTags = res.data.items
        })
      },
      // 获取服务类型
      getTypeOption () {
        getTypeService().then((res) => {
          let resTypeArray = res.data.items
          resTypeArray.forEach(element => {
            element.value = element.id
            element.label = element.title
          })
          this.serveTypeOptions = resTypeArray
        })
      },
      // 获取学历列表
      getEducations () {
        getBasicInformation().then(res => {
          let { educationList } = res.data
          this.educationOptions = transEducationData(educationList)
        })
      },
      // 选择等级对应改变薪资
      changeLevel (param) {
        this.level_id = param
        this.levelOptions.forEach((item) => {
          if (item.id === param) {
            this.AuntInfo.salary = item.salary
          }
        })
      },
      // 获取性格标签
      getCharacterTags () {
        getCharacterService().then((res) => {
          this.characterTags = res.data
        })
      },
      // 获取证书标签
      getCertificateTags () {
        let param = {}
        param.category = 'certificate-label'
        param.service_type_id = this.AuntInfo.service_type_id
        getCertificateService(param).then((res) => {
          this.certificateTags = res.data.items
        })
      },
      // 选择籍贯完成
      handleChangePlace (value) {
        this.AuntInfo.birthplace_province_id = value[0]
        this.AuntInfo.birthplace_city_id = value[1]
      },
      // 选择居住地完成
      handleChangeLive (value) {
        this.AuntInfo.addr_province = value[0]
        this.AuntInfo.addr_city = value[1]
      },
      // 移除图片
      handleRemove (file, fileList) {
        console.log(file, fileList)
      },
      // 预览图片
      handlePictureCardPreview (file) {
        this.dialogImageUrl = file.url
        this.dialogVisible = true
      },
      uploadSucc (file) {
        if (file.host) {
          this.ossHost = file.host
        } else {
          this.ossHost = 'https://deixian.oss-cn-beijing.aliyuncs.com'
        }
        console.log(file)
        if (file.use === 'cert') {
          this.certificateFileList.push({url: file.path})
          this.certificateImages.push(file.path)
          console.log(this.certificateImages)
        } else if (file.use === 'work') {
          this.workPhotosFileList.push({url: file.path})
          this.workPhotos.push(file.path)
        } else if (file.use === 'ayiVideo') {
          console.log(file.path)
          this.AuntInfo['video'] = file.path
        } else if (file.use === 'idCardFront') {
          this.AuntInfo['id_hold'] = file.path
        } else if (file.use === 'idCardReverseSide') {
          this.AuntInfo['id_back'] = file.path
        } else {
          this.AuntInfo.photo = file.path
        }
        console.log(this.AuntInfo.id_hold)
        console.log(this.AuntInfo.id_back)
        this.$forceUpdate()
      },
      removeFile (file) {
        // 从filelist展示列表中移除
        this.certificateFileList.forEach((element, index) => {
          if (element.url === file.file.url) {
            this.certificateFileList.splice(index, 1)
          }
        })
        this.workPhotosFileList.forEach((element, index) => {
          if (element.url === file.file.url) {
            this.workPhotosFileList.splice(index, 1)
          }
        })
        this.certificateImages.forEach((element, index) => {
          if (element === file.file.url) {
            this.certificateImages.splice(index, 1)
          }
        })
        this.workPhotos.forEach((element, index) => {
          if (element === file.file.url) {
            console.log(file.file.url)
            this.workPhotos.splice(index, 1)
          }
        })
      }
    }
  }
</script>
