<template>
  <div id="upload-images">
    <el-upload
      :class="updateStatus ? '' : 'no-border'"
      :disabled='updateStatus ? false : true'
      :file-list='fileList'
      action="default"
      :http-request="uploadAction"
      :headers="uploadHeaders"
      list-type="picture-card"
      :before-upload="beforeAvatarUpload"
      :limit="limit"
      :on-remove="remove"
      :on-preview="handlePictureCardPreview">
      <div class="needsclick">
        <i :class="updateStatus ? 'el-icon-plus' : false" class="needsclick"></i>
      </div>
    </el-upload>
    <el-dialog :visible.sync="dialogVisible">
      <img class="needsclick" width="100%" :src="dialogImageUrl" alt="">
    </el-dialog>
    <slot></slot>
  </div>
</template>
<script>
import { uploadImage, uploadoss, uploadFile } from '../service/loginServer.js'
export default {
  name: 'uploadImages',
  props: ['updateStatus', 'use', 'fileList'],
  data () {
    return {
      dialogVisible: false,
      dialogImageUrl: '',
      limit: 9,
      uploadHeaders: {
        'Authentication-Token': localStorage.getItem('token')
      }
    }
  },
  methods: {
    uploadImg (item) {
      console.log(item)
      let fd = new FormData()
      fd.append('photo_id', item.file)
      uploadImage(fd).then(res => {
        let params = {
          path: res.data.file_path,
          use: this.use
        }
        console.log(params)
        // 通知父级元素，返回图片路径
        this.$emit('uploadSucc', params)
      })
    },
    beforeAvatarUpload (file) {
      const isLt2M = file.size / 1024 / 1024 < 20
      const isCurType = /\.(jpg|jpeg|png|bmp|JPG|PNG|BMP)$/.test(file.name)
      if (!isLt2M) {
        this.$alert('上传图片大小不能超过 20MB!', '友情提示', {
          confirmButtonText: '确 定',
          callback: action => {}
        })
      }
      if (!isCurType) {
        this.$alert('上传图片类型不符合要求!', '友情提示', {
          confirmButtonText: '确 定',
          callback: action => {}
        })
      }
      return isLt2M && isCurType
    },
    uploadAction (item) {
      console.log(item)
      let ossParams = { 'upload_type': 'admin' }
      uploadoss(ossParams).then(res => {
        let params = {
          host: res.data.host
        }
        let fd = new FormData()
        let postfix = item.file.name.split('.')[item.file.name.split('.').length - 1]
        let filename = this.$md5(`${new Date().getTime()}_${item.file.name}`)
        fd.append('callback', res.data.callback)
        fd.append('success_action_status', '200')
        fd.append('OSSAccessKeyId', res.data.accessid)
        fd.append('policy', res.data.policy)
        fd.append('signature', res.data.signature)
        fd.append('key', `${res.data.dir}${filename}.${postfix}`)
        fd.append('host', res.data.host)
        fd.append('file', item.file)
        fd.append('expire', res.data.expire)
        fd.append('name', `${filename}.${postfix}`)
        params['fd'] = fd
        uploadFile(params.host, params.fd).then(res => {
          let result = {
            path: `${params.host}/${res.filename}`,
            host: `${params.host}`,
            use: this.use
          }
          // 通知父级元素，返回图片路径
          this.$emit('uploadSucc', result)
          console.log(this.fileList)
          if (this.fileList.length === 9) {
            // 处理不显示+号的样式暂没好的解决方法
          }
        })
      })
    },
    remove (file, fileList) {
      let params = {
        file,
        fileList,
        use: this.use
      }
      console.log(file)
      this.$emit('removeFile', params)
    },
    handlePictureCardPreview (file) {
      this.dialogImageUrl = file.url
      this.dialogVisible = true
    }
  }
}
</script>
<style>
#upload-images .el-upload-list--picture-card .el-upload-list__item-thumbnail {
  object-fit: cover;
}
</style>
<style scoped>
.no-border .el-upload--picture-card{
  border: none;
  background: none
}
.needsclick {
  object-fit: cover;
}
</style>



